/* Tour Overlay */
.tour-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  z-index: 9998;
  animation: fadeIn 0.3s ease;
}

/* Tour Spotlight/Card */
.tour-spotlight {
  position: fixed;
  z-index: 9999;
  animation: scaleIn 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.tour-position-center {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.tour-position-top {
  bottom: calc(100% + 20px);
  left: 50%;
  transform: translateX(-50%);
}

.tour-position-bottom {
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
}

.tour-card {
  background: white;
  border-radius: 16px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.4);
  width: 90vw;
  max-width: 500px;
  overflow: hidden;
  animation: slideUp 0.4s ease;
}

@keyframes scaleIn {
  from {
    opacity: 0;
    transform: scale(0.9);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes slideUp {
  from {
    transform: translateY(20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

/* Tour Header */
.tour-header {
  padding: 24px 24px 16px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 16px;
  border-bottom: 2px solid #e2e8f0;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
}

.tour-title {
  font-size: 1.25rem;
  font-weight: 700;
  margin: 0;
  line-height: 1.4;
}

.tour-close {
  background: rgba(255, 255, 255, 0.2);
  border: none;
  color: white;
  font-size: 2rem;
  line-height: 1;
  width: 36px;
  height: 36px;
  border-radius: 50%;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease;
  flex-shrink: 0;
}

.tour-close:hover {
  background: rgba(255, 255, 255, 0.3);
  transform: rotate(90deg);
}

/* Tour Body */
.tour-body {
  padding: 24px;
}

.tour-content {
  font-size: 1rem;
  line-height: 1.6;
  color: #4a5568;
  margin: 0;
}

/* Tour Footer */
.tour-footer {
  padding: 16px 24px 24px;
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.tour-progress {
  display: flex;
  justify-content: center;
  gap: 8px;
}

.tour-dot {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: #cbd5e0;
  transition: all 0.3s ease;
}

.tour-dot.active {
  background: #667eea;
  width: 24px;
  border-radius: 5px;
  animation: dotExpand 0.3s ease;
}

.tour-dot.completed {
  background: #48bb78;
}

@keyframes dotExpand {
  from {
    width: 10px;
  }
  to {
    width: 24px;
  }
}

.tour-actions {
  display: flex;
  justify-content: space-between;
  gap: 12px;
}

.tour-button {
  padding: 12px 24px;
  border: none;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
  flex: 1;
}

.tour-button-primary {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  box-shadow: 0 4px 12px rgba(102, 126, 234, 0.3);
}

.tour-button-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 16px rgba(102, 126, 234, 0.4);
}

.tour-button-secondary {
  background: white;
  color: #667eea;
  border: 2px solid #667eea;
}

.tour-button-secondary:hover {
  background: #f7fafc;
}

.tour-button:active {
  transform: translateY(0);
}

/* Tour Skip */
.tour-skip {
  padding: 0 24px 16px;
  text-align: center;
}

.tour-skip-button {
  background: none;
  border: none;
  color: #a0aec0;
  font-size: 0.875rem;
  cursor: pointer;
  padding: 8px;
  transition: color 0.2s ease;
}

.tour-skip-button:hover {
  color: #718096;
  text-decoration: underline;
}

/* Tour Restart Button */
.tour-restart-button {
  position: fixed;
  bottom: 24px;
  right: 24px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  border-radius: 50px;
  padding: 14px 24px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  box-shadow: 0 4px 16px rgba(102, 126, 234, 0.4);
  z-index: 999;
  display: flex;
  align-items: center;
  gap: 8px;
  transition: all 0.3s ease;
}

.tour-restart-button:hover {
  transform: translateY(-4px);
  box-shadow: 0 8px 24px rgba(102, 126, 234, 0.5);
}

.tour-restart-button:active {
  transform: translateY(-2px);
}

.tour-icon {
  font-size: 1.25rem;
}

.tour-text {
  font-size: 0.95rem;
}

/* Responsive */
@media (max-width: 768px) {
  .tour-card {
    width: 95vw;
    max-width: none;
  }

  .tour-header {
    padding: 20px 20px 12px;
  }

  .tour-title {
    font-size: 1.1rem;
  }

  .tour-body {
    padding: 20px;
  }

  .tour-content {
    font-size: 0.95rem;
  }

  .tour-button {
    padding: 10px 20px;
    font-size: 0.95rem;
  }

  .tour-restart-button {
    bottom: 16px;
    right: 16px;
    padding: 12px 20px;
    font-size: 0.875rem;
  }

  .tour-text {
    display: none;
  }

  .tour-icon {
    font-size: 1.5rem;
  }
}

