/* Linear Progress */
.linear-progress-container {
  width: 100%;
}

.linear-progress-bar {
  width: 100%;
  height: 8px;
  background-color: #e2e8f0;
  border-radius: 10px;
  overflow: hidden;
  position: relative;
}

.linear-progress-fill {
  height: 100%;
  border-radius: 10px;
  transition: width 0.3s ease;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding-right: 8px;
}

.linear-progress-label {
  font-size: 0.75rem;
  font-weight: 600;
  color: white;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);
}

/* Circular Progress */
.circular-progress-container {
  position: relative;
  display: inline-block;
}

.circular-progress-svg {
  transform: scale(1);
}

.circular-progress-bg {
  fill: none;
  stroke: #e2e8f0;
}

.circular-progress-fill {
  fill: none;
  stroke-linecap: round;
  transition: stroke-dashoffset 0.3s ease;
}

.circular-progress-label {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  font-weight: 700;
  color: #2d3748;
}

.circular-progress-value {
  font-size: 2rem;
}

.circular-progress-percent {
  font-size: 1rem;
  opacity: 0.7;
}

/* Step Progress */
.step-progress-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 20px 0;
}

.step-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  flex: 0 0 auto;
}

.step-circle {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: #e2e8f0;
  color: #a0aec0;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 1rem;
  transition: all 0.3s ease;
  border: 3px solid transparent;
}

.step-item.active .step-circle {
  background: #667eea;
  color: white;
  border-color: #667eea;
  box-shadow: 0 0 0 4px rgba(102, 126, 234, 0.2);
  animation: pulse 2s ease-in-out infinite;
}

.step-item.completed .step-circle {
  background: #48bb78;
  color: white;
  border-color: #48bb78;
}

.step-check {
  font-size: 1.25rem;
}

.step-label {
  font-size: 0.875rem;
  color: #718096;
  font-weight: 500;
  text-align: center;
  max-width: 100px;
}

.step-item.active .step-label {
  color: #2d3748;
  font-weight: 700;
}

.step-item.completed .step-label {
  color: #48bb78;
  font-weight: 600;
}

.step-connector {
  flex: 1;
  height: 3px;
  background: #e2e8f0;
  margin: 0 16px;
  transition: background-color 0.3s ease;
  max-width: 80px;
}

.step-connector.completed {
  background: #48bb78;
}

/* Spinner */
.spinner-container {
  display: inline-block;
}

.spinner-circle {
  border-style: solid;
  border-radius: 50%;
  animation: spin 0.8s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

/* Dots Loading */
.dots-loading {
  display: flex;
  gap: 8px;
  align-items: center;
  justify-content: center;
}

.dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  animation: dotBounce 1.4s ease-in-out infinite;
}

.dot:nth-child(1) {
  animation-delay: 0s;
}

.dot:nth-child(2) {
  animation-delay: 0.16s;
}

.dot:nth-child(3) {
  animation-delay: 0.32s;
}

@keyframes dotBounce {
  0%, 80%, 100% {
    transform: scale(0.8);
    opacity: 0.5;
  }
  40% {
    transform: scale(1.2);
    opacity: 1;
  }
}

/* Pulse Loading */
.pulse-loading {
  position: relative;
  display: inline-block;
}

.pulse-circle {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  opacity: 0.6;
  animation: pulseExpand 2s ease-out infinite;
}

.pulse-1 {
  animation-delay: 0s;
}

.pulse-2 {
  animation-delay: 1s;
}

@keyframes pulseExpand {
  0% {
    transform: scale(0.8);
    opacity: 0.7;
  }
  100% {
    transform: scale(2);
    opacity: 0;
  }
}

/* Responsive */
@media (max-width: 768px) {
  .step-progress-container {
    flex-wrap: wrap;
    gap: 20px;
  }

  .step-connector {
    display: none;
  }

  .step-item {
    flex: 1 1 auto;
    min-width: 80px;
  }

  .circular-progress-value {
    font-size: 1.5rem;
  }

  .circular-progress-percent {
    font-size: 0.875rem;
  }
}

